import React from "react";
import moment from "moment";
import uniq from "lodash/uniq";
import range from "lodash/range";
import merge from "lodash/merge";
import isEqual from "lodash/isEqual";
import cloneDeep from "lodash/cloneDeep";
import flattenDeep from "lodash/flattenDeep";
import MapNode from "./map/map";
import Detail from "./panels/detail";
import AlgalFlags from "./panels/flags";
import Settings from "./panels/settings";
import { loadJSON } from "./util";
import water from "./water";

// --------------------------------------------------------------------------
// Root container
export let AlgalRoot = React.createClass({

  getInitialState() {

    return {
      waterbodies: [],
      allWaterbodies: [],
      overview: [],
      years: [],
      focus: {},
      selectedDateIndex: 0,
      pixelQueryResult: { latlng: [], result: [] },
      display: 'base',
      displaySatellitePath: false,
      displayStateWideTrends: true,
    };

  },

  componentDidMount() {

    this.getLakeBodies();

    // TODO: Listen for alert updates
    // setInterval(this.loadAlerts, this.props.pollInterval || 2000);

  },

  render() {

    return (
      <div id="algal-vis-root" className="fullsize">
        <MapNode
          attribution={this.props.attribution}
          initPosition={this.props.initPosition}
          initZoomLevel={this.props.initZoomLevel}
          focus={this.state.focus}
          overview={this.state.overview}
          dateIndex={this.state.selectedDateIndex}
          display={this.state.display}
          displaySatellitePath={this.state.displaySatellitePath}
          displayStateWideTrends={this.state.displayStateWideTrends}
          baseMapService={this.props.baseMapService}
          satelliteImageryService={this.props.satelliteImageryService}
          handleMarkerClick={this.handleFocus}
          handlePixelQuery={this.handlePixelQuery}
        />
        <div id="component-container">
          <AlgalFlags
            waterbodies={this.state.waterbodies}
            allWaterbodies={this.state.allWaterbodies}
            years={this.state.years}
            handleFilter={this.handleFilter}
            handleFocus={this.handleFocus}
          />
          <Detail
            focus={this.state.focus}
            clearFocus={this.handleClearFocus}
            selectDate={this.handleSelectDate}
            pixelQueryResult={this.state.pixelQueryResult}
          />
          <Settings
            handleDisplaySatellitePath={this.handleDisplaySatellitePath}
            handleMapDisplay={this.handleMapDisplay}
            handleDisplayStateWideTrends={this.handleDisplayStateWideTrends}
          />
        </div>
      </div>
    );

  },

  getLakeBodies() {

    water.catalog(this.props.hyraxService, this.props.processedLakes, (err, catalog) => {
      // TODO: Add error message to side panel
      if (err) { console.error(err); return; }

      let waterbodies = [];
      let allWaterbodies = [];
      catalog.forEach( (item, i) => {

        if (!item.Processed) return;

        let waterbody = {
          displayName: item.DisplayName,
          name: item.Name,
          hasData: item.HasNetCDF,
          endDate: item.NetCDFLastDate ? moment(item.NetCDFLastDate) : null,
          flag: (item.LastGARFlag && item.LastGARFlag !== 'undefined') ? item.LastGARFlag : 'TBD',
          catalogUrl: `${this.props.hyraxService}/${item.Name}.nc`,
        };

        waterbodies.push(waterbody);
        allWaterbodies.push(waterbody);

        // Sort and set state only when it's last item
        // As of 2016-10-17 we are loading nearly 2000 lakes, this speeds up
        // load time
        if (i === catalog.length-1) {

          let sort_water = (a, b) => {
            var nameA = a.displayName;
            var nameB = b.displayName;
            if (nameA < nameB) {
              return -1;
            }
            if (nameA > nameB) {
              return 1;
            }
            return 0;
          };

          waterbodies.sort(sort_water);
          allWaterbodies.sort(sort_water);

          this.setState( { waterbodies, allWaterbodies } );

          // NOTE: State-wide lake names are hard coded for demo purposes.
          // TODO: This should be cached by the server, not calculated from the client
          // Get lake boundaries for selected lakes
          let overview = [];
          allWaterbodies.forEach( w => {
            if (this.props.stateWideMonitoredLakes.indexOf(w.name) > -1) {

              water.boundaries(w.catalogUrl, (err, result) => {
                if (err) {
                  console.error(err);
                  return;
                }

                // Determine current flag from the last value for demonstration
                // only
                let maxval = result.maxvals[result.maxvals.length-1];
                if (maxval < 20) {
                  w.flag = 'green';
                } else if (maxval >= 20 && maxval < 70) {
                  w.flag = 'amber';
                } else {
                  w.flag = 'red';
                }

                // Determine trend from the last 5 values for demonstration
                // purposes only
                w.trend = 'steady';
                let recent_medians = result.medians.slice(result.medians.length-5);
                let trend = 0;
                for (var i = 0; i < recent_medians.length-1; i++) {
                  if (recent_medians[i+1]-recent_medians[i] > 1) {
                    trend++;
                  } else if (recent_medians[i+1]-recent_medians[i] < -1) {
                    trend--;
                  }
                }
                if (trend > 1) {
                  w.trend = 'rising';
                } else if (trend < -1) {
                  w.trend = 'falling';
                }

                let latlng = L.latLng( (result.lat[0]+result.lat[1])/2,
                                       (result.lon[0]+result.lon[1])/2 );
                overview.push({ position: latlng, ...w});
                this.setState( { overview } );
              });

            }
          });

        }
      });
    });

  },

  handleFilter( location ) {
    // Change spaces to OR searches in the regex
    let locationRegex = new RegExp(location.replace(' ', '.'), 'i');

    let waterbodies = this.state.allWaterbodies.filter((element, index, array) => {
      // Last XX days
      // let timespan = date === "all" ||
      //             (Number(date) < 1000 && Math.abs(element.endDate.diff(moment(), 'days')) < Number(date));

      // return (timespan || (Number(date) >= element.startDate.year() && Number(date) <= element.endDate.year()))
      //     && (!location || locationRegex.test(element.name));

      return !location || locationRegex.test(element.name);
    });
    this.setState( { waterbodies } );
  },

  handleFocus( focus ) {

    // Locate the waterbody and pass reference to `focus`.
    this.state.waterbodies.forEach((waterbody, i) => {
      if (waterbody.name === focus) {

        // Check if the focus is in cache. If not, fetch it from server
        if (!waterbody.shapes) {

          water.boundaries(waterbody.catalogUrl, (err, data) => {
            if (err) { console.error(err); }

            waterbody = merge(waterbody, data);
            this.setState( { focus: waterbody, pixelQueryResult: [] } );
          });

        } else {
          this.setState( { focus: waterbody, pixelQueryResult: [] } );
        }

      }
    });

  },

  handleSelectDate( dateIndex ) {
    this.setState( { selectedDateIndex: dateIndex } );
  },
  handleClearFocus() {
    this.setState( { focus: {}, pixelQueryResult: [] } );
  },
  handleSettings( settings ) { this.setState( { settings } ); },
  handleMapDisplay( display ) { this.setState( { display } ); },
  handleDisplaySatellitePath( displaySatellitePath ) { this.setState( { displaySatellitePath } ); },
  handleDisplayStateWideTrends( displayStateWideTrends ) { this.setState( { displayStateWideTrends } ); },

  handlePixelQuery ( url, latlng, variable ) {
    loadJSON(url, (err, data) => {
      if (err) {
        console.error(err);
        return;
      }

      let result = flattenDeep(data[variable][variable]);
      if (isEqual(uniq(result), [null])) {
        this.setState({
          pixelQueryResult: { latlng: [], result: [] }
        });
      } else {
        this.setState({
          pixelQueryResult: { latlng, result }
        });
      }
    });
  },

});

// By default, leaflet looks at the <script> tag to work out relative icon paths.
// This is not sustainable for working in larger projects, so we set it manually.
// See https://github.com/Leaflet/Leaflet/issues/766#issuecomment-7741090
L.Icon.Default.imagePath = 'images/';

exports.default = AlgalRoot;