import xhr from "xhr";
import moment from "moment";

export let load = ( options, callback ) => {

  xhr(options, ( err, resp, body ) => {
    if (err) console.error(err); // TODO: Add error message to side panel
    callback(err, resp);
  });

};

export let loadJSON = ( url, callback ) => {

  load({
    url: url
  }, (err, resp) => {
    if (err) {
      callback(err); // TODO: Add error message to side panel
    }
    try {
      var body = resp.body.replace(/\bnan\b/g, 'null'); // NaN hack
      callback(null, JSON.parse(body));
    } catch (e) {
      console.error(url, e, body);
      callback(e); // TODO: Add error message to side panel
    }
  });

};

module.exports = {
  load,
  loadJSON,
};