/** Map view toggle from abstract to satellite
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */
import React from "react";
import moment from "moment";

export let Settings = React.createClass({

  render () {

    return (
      <div className="component secondary">
        <div className="component-title">Map Settings</div>
        <div className="component-content">
          <div className="component-body flex">
            <span className="flex-align">Map:</span>
            <input
              type="radio"
              id="map-display-base"
              name="map-display"
              value="base"
              onChange={this.handleMapDisplay}
              defaultChecked
            />
            <label className="flex-ible" htmlFor="map-display-base">
              <span className="radio-label">Streets</span>
            </label>
            <input
              type="radio"
              id="map-display-satellite"
              name="map-display"
              value="satellite"
              onChange={this.handleMapDisplay}
            />
            <label className="flex-ible" htmlFor="map-display-satellite">
              <span className="radio-label">Satellite</span>
            </label>
          </div>
          <div className="component-body flex">
            <span className="flex-ible">Satellite Acquisition Schedule:</span>
            <input
              className="flex-ible"
              type="checkbox"
              id="map-display-satellite-paths"
              name="map-display-satellite-paths"
              onChange={this.handleDisplaySatellitePath}
            />
          </div>
          <div className="component-body flex">
            <span className="flex-ible">State-wide Trends:</span>
            <input
              className="flex-ible"
              type="checkbox"
              id="map-state-wide-trends"
              name="map-state-wide-trends"
              onChange={this.handleDisplayStateWideTrends}
              defaultChecked={true}
            />
          </div>
        </div>
      </div>
    );

  },

  handleDisplaySatellitePath ( event ) {

    this.props.handleDisplaySatellitePath(event.target.checked);

  },

  handleDisplayStateWideTrends ( event ) {

    this.props.handleDisplayStateWideTrends(event.target.checked);

  },

  handleMapDisplay ( event ) {

    this.props.handleMapDisplay(event.target.value);

  }

});

exports.default = Settings;