// import L from 'leaflet';
import React from "react";
import ReactDOM from "react-dom";
import * as topojson from "topojson";
import L from "leaflet";
import {
  CircleMarker,
  GeoJSON,
  ImageOverlay,
  LayerGroup,
  Map,
  Marker,
  Popup,
  ScaleControl,
  TileLayer,
  ZoomControl,
} from "react-leaflet";
import minBy from "lodash/minBy";
import maxBy from "lodash/maxBy";
import moment from "moment";
import water from "../water";
import HomeControl from "./home";
import SearchControl from "./search";
import HyraxMapLayer from "./hyrax-map-layer";
import AlgalLegendControl from "./algal-legend";
import LogoControl from "./logos";
import { loadJSON } from "../util";

export let MapNode = React.createClass({

  getInitialState () {
    return {
      landsat: null
    };
  },

  componentDidMount () {
    loadJSON('data/wrs2_descending-topo.json', (err, obj) => {
      if (err) {
        console.error(err);
        return;
      }
      this.setState( { landsat: obj } );
    });
  },

  componentDidUpdate( prevProps, prevState ) {
    this.refs.hyrax.leafletElement.setOpacity(this.hasFocus() ? 1 : 0);
    if (!this.hasFocus()) { return; }

    // Automatically zoom to a lake
    if (this.props.focus.name !== prevProps.focus.name) {
      let southWest = [this.props.focus.lat[0], this.props.focus.lon[0]];
      let northEast = [this.props.focus.lat[1], this.props.focus.lon[1]];
      this.refs.map.leafletElement.fitBounds([southWest, northEast]);
    }
  },

  hasFocus () {
    return !!this.props.focus.name;
  },

  hyraxUrl () {

    if (!this.hasFocus()) { return; }

    // Ensure the date index doesn't overshoot. This is due to React updating
    // only one prop at a time, where as we need both updated at the same time.
    let dateIndex = Math.min(this.props.dateIndex, this.props.focus.shapes[0]-1);

    let ijsn = water.constructUrl(this.props.focus.catalogUrl,
                                  this.props.focus.variable,
                                  [
                                    [dateIndex, 1, dateIndex],
                                    [0, 1, this.props.focus.shapes[1]-1],
                                    [0, 1, this.props.focus.shapes[2]-1]
                                  ]);

    return ijsn;

  },

  hyraxPixelUrl ( latlng ) {

    if (!this.hasFocus()) { return; }

    let focus = this.props.focus;
    let [lat, lng] = latlng;
    let [[minY, maxY], [minX, maxX]] = [focus.lat, focus.lon];

    let y = Math.round((lat - minY) / (maxY - minY) * this.props.focus.shapes[1]);
    let x = Math.round((lng - minX) / (maxX - minX) * this.props.focus.shapes[2]);

    let ijsn = water.constructUrl(this.props.focus.catalogUrl,
                                  this.props.focus.variable,
                                  [
                                    [0, 1, this.props.focus.shapes[0]-1],
                                    [y, 1, y],
                                    [x, 1, x]
                                  ]);

    return ijsn;

  },

  render() {

    let tileLayer = () => {
      if (this.props.display === "base") {
        return (
          <TileLayer ref="basemap"
            url={this.props.baseMapService}
            attribution={this.props.attribution}
          />
        );
      } else if (this.props.display === "satellite") {
        return (
          <TileLayer ref="satellitemap"
            url={this.props.satelliteImageryService}
            attribution={this.props.attribution}
          />
        );
      };
    };

    let renderAlgalLegendControl = () => {
      if (this.hasFocus()) {
        return (
          <AlgalLegendControl />
        );
      }
    };

    // See Landsat 8 Acquisition Calendar
    // http://landsat.usgs.gov/tools_L8_acquisition_calendar.php
    // The maths seems simple but is a little more complex, so look-up is easier
    let wrs2Schedule = [
      [110, 126, 142, 158, 174, 190, 206, 222, 5, 21, 37, 53, 69, 85],
      [101, 117, 133, 149, 165, 181, 197, 213, 229, 12, 28, 44, 60, 76, 92],
      [108, 124, 156, 172, 188, 204, 220, 3, 19, 35, 51, 67, 83],
      [99, 115, 131, 147, 163, 179, 195, 211, 227, 10, 26, 42, 58, 74, 90],
      [106, 122, 138, 154, 170, 186, 202, 218, 1, 17, 33, 49, 65, 81, 97],
      [113, 129, 145, 161, 177, 193, 209, 225, 8, 24, 40, 56, 72, 88],
      [104, 120, 136, 152, 168, 184, 200, 216, 232, 15, 31, 47, 63, 79, 95],
      [111, 127, 143, 159, 175, 191, 207, 223, 6, 22, 38, 54, 70, 86],
      [102, 118, 134, 150, 166, 182, 198, 214, 230, 13, 29, 45, 61, 77, 93],
      [109, 125, 141, 157, 173, 189, 205, 221, 4, 20, 36, 52, 68, 84],
      [100, 116, 132, 148, 164, 180, 196, 212, 228, 11, 27, 43, 59, 75, 91],
      [107, 123, 139, 155, 171, 187, 203, 219, 2, 18, 34, 50, 66, 82],
      [98, 114, 130, 146, 162, 178, 194, 210, 226, 9, 25, 41, 57, 73, 89],
      [105, 121, 137, 153, 169, 185, 201, 217, 233, 16, 32, 48, 64, 80, 96],
      [112, 128, 144, 160, 176, 192, 208, 224, 7, 23, 39, 55, 71, 87],
      [103, 119, 135, 151, 167, 183, 199, 215, 231, 14, 30, 46, 62, 78, 94],
    ];

    let daysTilNextAcquisition = path => {

      let wrs2StartDate = moment('2016-10-16');
      let today = moment();
      let acquisitionIndex = -1;
      for (let i = 0; i < wrs2Schedule.length; i++) {
        if (wrs2Schedule[i].includes(path)) {
          acquisitionIndex = i;
          break;
        }
      }
      let todayAcquisitionIndex = today.diff(wrs2StartDate, 'days') % 16 + 1;
      let nextDays = ((acquisitionIndex-1) - (todayAcquisitionIndex-1) + 16) % 16 + 1;
      return nextDays % 8; // Landsat 8 and 7 interleave 8 days apart

    };

    let renderLandsatPath = () => {
      if (this.state.landsat) {

        let topo = this.state.landsat;

        let satelliteOverpass = (feature) => {
          let labels = feature.map( f => {
            let d = daysTilNextAcquisition(f.properties.PATH);
            let icon = L.divIcon({
              className: 'map-text',
              iconSize: [50, 50],
              iconAnchor: [20, 10],
              html: d == 0 ? 'Today' :
                      `in ${d} ${d == 1 ? 'day' : 'days'}`,
            });

            let coordinates = f.geometry.coordinates[0];
            let lats = coordinates.map(c => c[1]);
            let lngs = coordinates.map(c => c[0]);
            let lat = (minBy(lats) + maxBy(lats)) / 2;
            let lng = (minBy(lngs) + maxBy(lngs)) / 2;
            return (
              <Marker
                position={[lat,lng]}
                draggable={false}
                icon={icon}
              />
            );
          });

          return labels;
        };

        return Object.keys(topo.objects).map( k => {
          let obj = topojson.feature(topo, topo.objects[k]);
          return (
            <div>
              <GeoJSON
                data={obj}
                key={k}
                className='satellite-path'
              />
              {satelliteOverpass(obj.features)}
            </div>
          );
        });
      }
    };

    let renderOverview = () => {
      return this.props.overview.map( waterbody => {

        let trend = waterbody.trend.charAt(0).toUpperCase() + waterbody.trend.slice(1);
        let symbol = {
          'steady': `<span style="font: 125% sans-serif; line-height: 12px;">◆</span>`,
          'rising': `<span style="font: 100% sans-serif; line-height: 12px;">▲</span>`,
          'falling': `<span style="font: 75% sans-serif; line-height: 12px;">▼</span>`,
        };

        let html = `
          <span class="title">${waterbody.displayName}</span>
          <p>${symbol[waterbody.trend]} ${trend}</p>
        `;

        let flagClass = {
          'TBD': 'marker-none',
          'green': 'marker-green',
          'amber': 'marker-yellow',
          'red': 'marker-red',
        };

        let trendClass = {
          'steady': 'marker-none',
          'rising': 'marker-rising',
          'falling': 'marker-falling',
        };

        let icon = L.divIcon({
          html,
          iconSize: [125, 30], // NOTE: Leaflet will set 12x12 px otherwise
          bgPos: [0, 0],
          className: `leaflet-state-wide-marker ${trendClass[waterbody.trend]} ${flagClass[waterbody.flag]}`,
        });

        return (
          <Marker
            icon={icon}
            position={waterbody.position}
            onClick={() => {this.handleMarkerClick(waterbody.name);}}
            interactive={true}
          />
        );
      });
    };

    return (
      <Map
        center={this.props.initPosition}
        zoom={this.props.initZoomLevel}
        className="fullsize"
        ref="map"
        zoomControl={false}
        minZoom={7}
        maxZoom={17}
        markerZoomAnimation={false}
      >
        {tileLayer()}
        <LayerGroup id="satellite-paths-group" ref="satellite_paths">
          {this.props.displaySatellitePath ? renderLandsatPath() : null}
        </LayerGroup>
        <HyraxMapLayer ref="hyrax"
          url={this.hyraxUrl()}
          variable={this.props.focus.variable}
          interactive={true}
          handleClick={this.handleHyraxMapLayerClick}
        />
        <LayerGroup id="map-locations-group" ref="markers">
          {this.props.displayStateWideTrends ? renderOverview() : null}
        </LayerGroup>

        <HomeControl
          home={[this.props.initPosition]}
          zoom={this.props.initZoomLevel}
          position="topright"
        />
        <ZoomControl position="topright" />
        <SearchControl position="topright" />
        <LogoControl />
        <ScaleControl />
        {renderAlgalLegendControl()}
      </Map>
    );

  },

  handleMarkerClick ( focus ) {

    this.props.handleMarkerClick( focus );

  },

  handleHyraxMapLayerClick ( latlng ) {

    let coords = [latlng.lat, latlng.lng];
    let url = this.hyraxPixelUrl(coords);
    let v = this.props.focus.variable;
    this.props.handlePixelQuery(url, coords, v);

  }

});

exports.default = MapNode;