/** Plot the trend of lake turbidity over time
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */
import * as d3 from "d3";
import zip from "lodash/zip";
import flatMap from "lodash/flatMap";
import isEqual from "lodash/isEqual";
import React from "react";
import moment from "moment";
import { colourStops, colourDomain } from "../map/hyrax-map-layer";

export let Detail = React.createClass({

  componentDidMount () {
    this.margin = {top: 80, right: 40, bottom: 55, left: 50};
    this.width = 375 - this.margin.left - this.margin.right;
    this.height = 220 - this.margin.bottom - this.margin.top;

    this.x = d3.scaleTime().range([0, this.width]);
    this.y = d3.scaleLinear().range([this.height, 0]);

    this.xAxis = d3.axisBottom()
                   .scale(this.x)
                   .ticks(10, '%b %Y');

    this.yAxis = d3.axisLeft()
                   .scale(this.y)
                   .ticks(5);

    this.minMax = d3.area().x(d => this.x(d.date))
                           .y0(d => this.y(d.min))
                           .y1(d => this.y(d.max));

    this.medianLine = d3.line().x(d => this.x(d.date))
                               .y(d => this.y(d.median));

    this.selectedDateLine = d3.line().x(d => this.x(d[0]))
                                     .y(d => this.y(d[1]));

    this.algalSVG = d3.select('#algalTrend')
                      .append('svg')
                      .attr('class', 'chart')
                      .attr('width', this.width + this.margin.left + this.margin.right)
                      .attr('height', this.height + this.margin.bottom + this.margin.top);

    this.algalTrend = this.algalSVG.append('g')
                                   .attr('class', 'container')
                                   .attr('transform', `translate(${this.margin.left},${this.margin.top})`);

    this.xAxisContainer = this.algalTrend.append('g')
                                         .attr('class', 'x axis noselect')
                                         .attr('transform', `translate(0,${this.height})`);

    this.yAxisContainer = this.algalTrend.append('g')
                                         .attr('class', 'y axis noselect');

    this.yAxisContainer.append('text')
                       .attr('y', -65)
                       .attr('x', -this.margin.left + 20)
                       .attr('text-anchor', 'start')
                       .text('Turbidity over time, range and median')
                       .attr('class', 'title noselect');

    this.yAxisContainer.append('text')
                       .attr('y', -26)
                       .attr('x', -this.margin.left + 20)
                       .attr('text-anchor', 'start')
                       .text('TSM (mg/L)')
                       .attr('class', 'noselect');

  },

  componentDidUpdate ( prevProps, prevState ) {

    let pixelQueryChanged = !isEqual(prevProps.pixelQueryResult,
                                     this.props.pixelQueryResult);

    if (prevProps.focus.name === this.props.focus.name && !pixelQueryChanged) { return; }

    if (!this.hasFocus()) {
      this.selectingDates = false;
      return;
    }

    this.algalTrend.selectAll("path.line, path.area, rect, circle.point, linearGradient, text.link").remove();

    let data = this.props.focus.dates.map((date, i) => {
      return {
        date: date.toDate(),
        min: this.props.focus.minvals[i],
        median: this.props.focus.medians[i],
        max: this.props.focus.maxvals[i]
      }
    });

    // Export data link
    this.yAxisContainer.append('text')
                       .attr('y', -65)
                       .attr('x', this.width)
                       .attr('text-anchor', 'end')
                       .text('[view data]')
                       .attr('class', 'title link')
                       .on('click', () => {
                          // This opens the JSON in a new window or downloads it.
                          window.open('data:application/json;' + (window.btoa?'base64,'+window.btoa(JSON.stringify(data)):JSON.stringify(data)));
                       })
                       .append('title')
                       .text('Opens exported data in a new window.');

    this.combinedValues = flatMap(data, d => {
      return [d.min, d.median, d.max];
    });

    let yExtent = d3.extent(this.combinedValues);
    this.x.domain(d3.extent(data, d => d.date));
    this.y.domain(yExtent);

    this.xAxisContainer.call(this.xAxis)
                       .selectAll('text')
                       .style('text-anchor', 'end')
                       .attr('dx', '-.8em')
                       .attr('dy', '.15em')
                       .attr('transform', 'rotate(-45)' );
    this.yAxisContainer.call(this.yAxis);

    // Calculate gradient stops based on y-value
    const interpolateStops = ([min, max]) => {
      // Get the two ends
      let stops = [
        { offset: '0%', colour: colourDomain(min).hex() },
      ];

      // Calculate any stops inside the two ends
      const getRatio = r => (r - min) / (max - min) * 100;

      colourStops.forEach( cs => {
        if (min < cs && cs < max) {
          stops.push({ offset: `${getRatio(cs)}%`, colour: colourDomain(cs).hex() });
        }
      });

      stops.push({ offset: '100%', colour: colourDomain(max).hex() });

      return stops;
    };

    let gradientStops = interpolateStops(yExtent);

    // Area gradient fill
    // http://bl.ocks.org/d3noob/4433087
    this.algalTrend.append('linearGradient')
      .attr('id', 'algae-traffic')
      .attr('gradientUnits', 'userSpaceOnUse')
      .attr('x1', this.x(0)).attr('y1', this.height)
      .attr('x2', this.x(0)).attr('y2', 0)
    .selectAll('stop')
      .data(gradientStops)
    .enter().append('stop')
      .attr('offset', function(d) { return d.offset; })
      .attr('stop-color', function(d) { return d.colour; });

    this.algalTrend.append('path')
                   .attr('class', 'area light')
                   .attr('fill', 'url(#algae-traffic)')
                   .datum(data)
                   .attr('d', this.minMax);

    // When the user clicks on the map overlay, show the individual measures
    // on that point.
    this.yAxisContainer.select('text.latlng').remove();
    let pqr = this.props.pixelQueryResult;
    if ( pqr.result && pqr.result.length) {

      let pixelQueryData = zip(
        this.props.focus.dates.map((date) => { return date.toDate() }),
        pqr.result
      );

      let points = this.algalTrend.selectAll('circle')
                                  .data(pixelQueryData);
      points.enter()
            .append('circle')
            .attr('class', 'point')
            .attr('r', 1.5)
            .attr('cx', d => this.x(d[0]) )
            .attr('cy', d => d[1] ? this.y(d[1]) : -100 );

      points.exit().remove();

      let coords = '';
      if (pqr.latlng.length === 2) {
        coords = `Points at ${Number(pqr.latlng[0]).toFixed(6)}, ${Number(pqr.latlng[1]).toFixed(6)}`;
      }

      this.yAxisContainer.append('text')
                   .attr('y', -45)
                   .attr('x', -this.margin.left + 20)
                   .attr('text-anchor', 'start')
                   .text(coords)
                   .attr('class', 'noselect latlng');

    } else {

      this.algalTrend.append('path')
                     .attr('class', 'line')
                     .datum(data)
                     .attr('d', this.medianLine);

    }

    this.interactionArea = this.algalTrend.append('rect')
                                          .attr('class', 'interaction-area')
                                          .attr('width', this.x.range()[1])
                                          .attr('height', this.y.range()[0])
                                          .attr('x', 0)
                                          .attr('y', 0);

    // Unless you know what you are doing, do not change the following to
    // arrow functions. It breaks d3 selections in getClosestDateFromRange().
    const self = this;
    let range = 3;
    this.interactionArea.on('click', function () {
      self.getClosestDateFromRange(this, range);
      return false;
    });
    this.interactionArea.on('mousedown', function () {
      self.selectingDates = true;
      self.getClosestDateFromRange(this, range);
    });
    this.interactionArea.on('mousemove', function () {
      if (self.selectingDates) {
        self.getClosestDateFromRange(this, range);
      }
    });
    this.interactionArea.on('mouseup', function () {
      self.selectingDates = false;
    });
    this.interactionArea.on('mosueout', function () {
      self.selectingDates = false;
    });

    // console.log(this.props.focus.dates.length-1);
    if (prevProps.focus.name !== this.props.focus.name || !pixelQueryChanged) {
      this.handleDateChange(this.props.focus.dates.length-1);
    }

  },

  hasFocus () {
    return !!Object.keys(this.props.focus).length;
  },

  handleDateChange (dateIndex) {

    let activeDate = this.props.focus.dates[dateIndex];

    this.algalTrend.selectAll('path.active, text.active').remove();
    this.algalTrend.append('path')
                   .attr('class', 'active')
                   .datum([
                     [activeDate, 0],
                     [activeDate, d3.max(this.combinedValues)]
                   ])
                   .attr('d', this.selectedDateLine);
    this.algalTrend.append('text')
                   .attr('class', 'active noselect')
                   .style('text-anchor', 'middle')
                   .attr('x', this.x(activeDate))
                   .attr('y', -5)
                   .text(moment(activeDate).format('D MMM YYYY'));

    this.props.selectDate(dateIndex);

  },

  getClosestDateFromRange (container, threshold) {

    if (!this.hasFocus()) { return; }

    var x = d3.mouse(container)[0];
    for (let i = 0; i < this.props.focus.dates.length; i++) {
      var d = this.props.focus.dates[i].toDate();
      if (Math.abs(this.x(d) - x) <= threshold) {
        this.handleDateChange(i);
        return;
      }
    };

  },

  // handleShowOnMap () {

  //   this.props.handleShowOnMap(this.props.focus.name);

  // },

  render () {

    return (
      <div className={this.hasFocus() ? "component trend" : "component trend hide"}>
        <div className="component-title">
          {this.props.focus.name}
          <span className="component-topright"><a href="#" onClick={this.props.clearFocus} title="close selection">x</a></span>
        </div>
        <div className="component-content scrollable">
          <div id="algalTrend"></div>
          <div className="component-body">
          </div>
        </div>
      </div>
    );

  }

});

exports.default = Detail;