import { PropTypes } from "react";
import L from 'leaflet';
import { MapControl } from 'react-leaflet';
import 'leaflet-geocoder-mapzen';

// React-Leaflet addition
export default class SearchControl extends MapControl {

  componentWillMount () {
    this.leafletElement = L.control.geocoder('search-XssRbML', this.props);
  }

}

// Until static initialisers are out of TC39 stage 0: strawman, we will have to
// declare this out of class.
// See http://facebook.github.io/react/blog/2015/01/27/react-v0.13.0-beta-1.html#es7-property-initializers
SearchControl.propTypes = {};