/** Create a PNG overlay for React-Leaflet from Hyrax service endpoint
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */
import moment from "moment";
import chroma from "chroma-js";
import { React, PropTypes } from "react";
import { imageOverlay, latLngBounds } from "leaflet";
import { MapLayer } from "react-leaflet";
import { loadJSON } from "../util";

// In this case, colour map uses a traffic-light convention red, amber, and
// green to convey potential risks.
let green = chroma('#99C794');
let yellow = chroma('#FAC863');
let red = chroma('#EC5f67');
let colourMap = chroma.scale([
  green.brighten(1), green,
  yellow.brighten(1), yellow,
  red.brighten(1), red
]).mode('lab'); // L*a*B blending mode for chroma-js
export let colourStops = [
  0, 19,
  21, 70,
  71, 100
];
// Make the colour domain available for other things
export let colourDomain = colourMap.domain(colourStops);

 // Save images to client memory to reduce load time
 // This cache is shared across every instance of HyraxMapLayer
let imageDataCache = {};

// React-Leaflet addition
// See https://github.com/PaulLeCam/react-leaflet/blob/master/src%2FImageOverlay.js
//
// Given a Hyrax endpoint URL, this layer adds an ImageOverlay layer on Leaflet
// and refreshes its image content based on the Hyrax service endpoint.
// Unlike WMS services, images are constructed client-side, and it offers
// flexibility of changing colour maps and thresholds without re-querying the
// server.
export default class HyraxMapLayer extends MapLayer {

  // Setup an ImageOverlay element 'onload'
  componentWillMount () {
    super.componentWillMount();
    const { map: _map, layerContainer: _lc, ...props } = this.props;
    this.leafletElement = imageOverlay('#', [[0, 0], [0, 0]], props);
    this.reloadComponent();
  }

  // React way to check if the Hyrax service query has been updated. If so,
  // reloadComponent() will run by the framework
  componentDidUpdate (prevProps) {
    if (this.props.url !== prevProps.url) {
      this.reloadComponent();
    }
    if (this.props.opacity !== prevProps.opacity) {
      this.leafletElement.setOpacity(this.props.opacity);
    }
  }

  // Create a new Canvas element and converts the data into pixels
  plot (image) {
    this.leafletElement.off('click', this.getLocalCoordinates, this);

    let canvas = document.createElement('canvas');
    canvas.width = image.width;
    canvas.height = image.height;

    let ctx = canvas.getContext('2d');
    ctx.putImageData(image.data, 0, 0);

    this.leafletElement._bounds = latLngBounds(image.extent);
    this.leafletElement.setUrl(canvas.toDataURL('image/png'));
    this.leafletElement._reset();

    this.leafletElement.on('click', this.getLocalCoordinates, this);
  }

  getLocalCoordinates (event) {

    this.props.handleClick(event.latlng);

  }

  // Load the Hyrax endpoint data and refresh layer
  reloadComponent () {

    if (!this.props.url) {
      return;
    }

    if (this.props.url in imageDataCache) {
      this.plot(imageDataCache[this.props.url]);
      return;
    }

    let url = this.props.url;
    let variable = this.props.variable;

    loadJSON(url, (err, data) => {
      if (err) {
        if (!url) {
          console.warn(err);
        }
        return;
      }
      if (!data) {
        console.error("No data returned: ", data);
        return;
      }

      let canvas = document.createElement('canvas');
      let image = {};

      let obj = data[variable];
      if (!obj) {
        console.warn('No data found for variable', variable, 'in', url);
        return;
      }

      // FIXME Duplicate values -_-
      if (!obj.x) obj.x = obj.lon;
      if (!obj.lon) obj.lon = obj.x;
      if (!obj.y) obj.y = obj.lat;
      if (!obj.lat) obj.lat = obj.y;

      // Setup colour map, extent of dataset, as well as a buffer to store
      // values in. By this point, we can safely assume that Hyrax has returned
      // us data to plot.

      // Add in offset for some lakes, 0 works for most cases
      let x_offset = 0,
          y_offset = 0;

      let left = obj.x[0] - x_offset;
      let right = obj.x[obj.x.length-1] + x_offset;
      let top = obj.y[0] - y_offset;
      let bottom = obj.y[obj.y.length-1] + y_offset;

      image.extent = [[top, left], [bottom, right]];
      image.height = obj.y.length;
      image.width = obj.x.length;
      let ctx = canvas.getContext('2d');

      // This is the reliable way to create an ImageData buffer cross-browser.
      let imageData = ctx.createImageData(image.width, image.height);
      let d = imageData.data;

      // Iterate through each pixel channel and translates them onto the buffer
      for (let y = 0; y < obj.y.length; y++) {
        for (let x = 0; x < obj.x.length; x++) {

          let i = (obj.x.length * y + x) * 4;
          let value = obj[variable][0][y][x];
          if (value) {
            let v = colourDomain(value).rgb();
            d[i    ] = v[0];    // red
            d[i + 1] = v[1];    // green
            d[i + 2] = v[2];    // blue
            d[i + 3] = 255;     // alpha
          } else {
            d[i    ] = 0;       // red
            d[i + 1] = 0;       // green
            d[i + 2] = 0;       // blue
            d[i + 3] = 0;       // alpha
          }

        }
      }

      // Pass the buffer into cache and plot it
      image.data = imageData;
      imageDataCache[url] = image;
      this.plot(image);
    });
  }

  render () {
    return null;
  }

}

HyraxMapLayer.propTypes = {
  attribution: PropTypes.string,
  opacity: PropTypes.number,
  url: PropTypes.string,
  variable: PropTypes.string,
  interactive: PropTypes.bool
};