import L from 'leaflet';
import { PropTypes } from 'react';
import { MapControl } from 'react-leaflet';

L.Control.Logos = L.Control.extend({
  options: {
    position: 'bottomleft',
  },

  onAdd: function (map) {
    var name = 'leaflet-control-logos',
        container = L.DomUtil.create('div', name);

    this._map = map;

    this._logos = this._createLogos(container);

    return container;
  },

  _createLogos: function (container) {
    var image = L.DomUtil.create('img', 'logos', container);
    image.src="images/logos.png";
  }

});

L.control.logos = function(options) {
  return new L.Control.Logos(options);
};

// React-Leaflet addition
export default class LogoControl extends MapControl {

  componentWillMount () {
    this.leafletElement = L.control.logos(this.props);
  }

}

LogoControl.propTypes = {
  position: PropTypes.string,
  legendTitle: PropTypes.string,
  greenLevel: PropTypes.string,
  amberLevel: PropTypes.string,
  redLevel: PropTypes.string
};