/** Offer a list of recent flagged lakebodies and their severity.
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */
import React from "react";
import { alertPassesFilter } from "../util";

export let AlgalFlags = React.createClass({

  render() {

    let algalFlags = () => {
      if (this.props.waterbodies.length) {
        return this.props.waterbodies.map( ( waterbody, i ) => {

          let row = (hasData) => {
            if (hasData) {
              return (
                <a href="#"
                   onClick={this.handleClick.bind(this, waterbody.name)}>
                  <span>{waterbody.displayName}</span>
                </a>
              );
            }
            return (
              <span className="component-no-data">{waterbody.displayName}</span>
            );
          };

          return (
            <div className="component-row flex" key={i}>
              <span className="flex-align">{waterbody.endDate ? waterbody.endDate.format("DD MMM YYYY") : "--"}</span>
              <span className="flex-ible">
                {row(waterbody.hasData)}
              </span>
              <span className="flex-align">{waterbody.hasData ? waterbody.flag : '--'}</span>
            </div>
          );

        });
      }
      else if (this.props.allWaterbodies.length > 0 &&
               this.props.waterbodies.length < 1) {
        return (
          <div className="component-row" key="no-result">
            No lakes match. Please try a different term.
          </div>
        );
      }
      else {
        return;
      }
    };

    let locationFilter = (
      <input ref="location" type="search"
             onChange={this.handleFilter} placeholder="e.g. Lake Burley Griffin" />
    );

    return (
      <div className="component">
        <div className="component-title">Algae Flags</div>
        <div className="component-options">
          <label className="flex">
            <span>Search location:</span>
            {locationFilter}
          </label>
        </div>
        <div className="component-header flex">
          <span className="flex-align">Latest available</span>
          <span className="flex-ible">Waterbody name</span>
          <span className="flex-align">Flag</span>
        </div>
        <div className="component-content scrollable">
          {algalFlags()}
        </div>
      </div>
    );
  },

  handleFilter( event ) {

    this.props.handleFilter(this.refs.location.value);

  },

  handleClick( name ) {

    this.props.handleFocus(name);

  }

});

exports.default = AlgalFlags;