/** Algal alerts map legend for React-Leaflet
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */

import L from 'leaflet';
import { PropTypes } from 'react';
import { MapControl } from 'react-leaflet';

L.Control.AlgalLegend = L.Control.extend({
  options: {
    position: 'bottomright',
    legendTitle: 'Turbidity levels',
    greenLevel: '< 20 mg/L',
    amberLevel: '≥ 20 to < 70 mg/L',
    redLevel: '≥ 70 mg/L',
  },

  onAdd: function (map) {
    var name = 'leaflet-control-legend',
        container = L.DomUtil.create('div', name);

    this._map = map;

    this._algalLegend = this._createLegend(
      this.options.legendTitle,
      this.options.greenLevel,
      this.options.amberLevel,
      this.options.redLevel,
      container
    );

    return container;
  },

  _createLegend: function (legendTitle, green, amber, red, container) {
    var title = L.DomUtil.create('p', 'legend-title', container);
    var redLegend = L.DomUtil.create('p', 'legend-text', container);
    var amberLegend = L.DomUtil.create('p', 'legend-text', container);
    var greenLegend = L.DomUtil.create('p', 'legend-text', container);
    var tempText = L.DomUtil.create('p', 'legend-text', container);

    title.innerHTML = legendTitle;
    redLegend.innerHTML = '<span class="legend-red">Red</span> ' + red;
    amberLegend.innerHTML = '<span class="legend-amber">Amber</span> ' + amber;
    greenLegend.innerHTML = '<span class="legend-green">Green</span> ' + green;
    tempText.innerHTML = 'This is a work in progress.<br>Colours and metrics are indicative only.';
  }

});

L.control.algalLegend = function(options) {
  return new L.Control.AlgalLegend(options);
};

// React-Leaflet addition
export default class AlgalLegendControl extends MapControl {

  componentWillMount () {
    this.leafletElement = L.control.algalLegend(this.props);
  }

}

AlgalLegendControl.propTypes = {
  position: PropTypes.string,
  legendTitle: PropTypes.string,
  greenLevel: PropTypes.string,
  amberLevel: PropTypes.string,
  redLevel: PropTypes.string
};