/** Lightweight endpoint wrapper for algal blooming Hyrax service endpoint
 *  Author: Xavier Ho <xavier.ho@csiro.au>
 */
import moment from 'moment';
import util from './util';

// Return a list of lakes available on the Hyrax service
export let catalog = ( hyraxUrl, processedLakesUrl, callback ) => {

  util.loadJSON(processedLakesUrl, (err, data) => {
    callback(err, data);
  });

};

// Return the URL endpoint for a given catalog item, variable of interest, and
// the indices to take. If boundaries are not specified, it will query for
// everything.
export let constructUrl = ( catalog_url, variable, boundaries ) => {

  let param = (variable, boundaries) => {
    let indices = boundaries ?
      boundaries.map(([start, step, stop]) => {
        return `[${start}:${step}:${stop}]`;
      }).join('')
      : '';
    return `${variable}${indices}`;
  };

  if (Array.isArray(variable)) {
    let params = variable.map((v, i) => param(v, boundaries[i]));
    var ijsn = `${catalog_url}.ijsn?${params.join(',')}`;
  } else {
    var ijsn = `${catalog_url}.ijsn?${param(variable, boundaries)}`;
  }

  return ijsn;

};

// Return the values from a variable using the endpoint provided by constructUrl()
export let values = ( catalog_url, variable, boundaries, callback ) => {

  let ijsn = constructUrl(catalog_url, variable, boundaries);

  util.loadJSON(ijsn, (err, obj) => {
    callback(err, obj);
  });
};

// Return the dimensions (such as the shape of a 2d array)
export let dimensions = ( catalog_url, callback ) => {
  let ijsn = catalog_url + '.ijsn';

  util.loadJSON(ijsn, (err, obj) => {
    callback(err, obj);
  });

};

// Access Hyrax's w10n service to query just the variable names.
// Useful if the variable name is unknown to the client.
export let variables = ( catalog_url, callback ) => {

  let w10n = catalog_url.replace('/opendap/', '/opendap/w10n/') + '/?output=json';
  util.loadJSON(w10n, (err, body) => {
    callback(err, body);
  });

};

// Fetch the global attributes that are stored in each NetCDF to display
// last processed date, display name, and flags
export let attributes = ( catalog_url, callback ) => {

  variables( catalog_url, (err, resp) => {
    if (err) { callback(err); }

    // NetCDF attributes come in an array of {name, value} pairs. For it to be
    // JSON friendly, we will parse them into object form before passing back.
    let attrs = {};
    resp.attributes.forEach(r => {
      if (r.name === 'NC_GLOBAL') {
        r.attributes.forEach(attr => {
          let value = attr.value.length === 1 ? attr.value[0] : attr.value;
          attrs[attr.name] = value;
        });
      }
    });
    callback(null, attrs);
  });

};

// Return the first and last value of [time, y, x] for our dataset.
// Useful for granting an overview of the data
export let boundaries = ( catalog_url, callback ) => {

  variables( catalog_url, (err, resp) => {
    if (err) { callback(err); }

    // NOTE: `leaves` come in time, lat, lon.
    // This order is not defined by w10n service, but a natural ordering of netcdf in our computation
    let shapes = [0, 0, 0];

    resp.leaves.forEach(leaf => {
      if (leaf.name === 'time') {
        shapes[0] = leaf.shape[0];
      } else if (leaf.name === 'lat' || leaf.name === 'y') {
        shapes[1] = leaf.shape[0];
      } else if (leaf.name === 'lon' || leaf.name === 'x') {
        shapes[2] = leaf.shape[0];
      }
    });

    // NOTE: Variables are stored within `nodes`,
    // with WQ_data being the main one.
    let variables = ["WQ_data", "WQ_medians", "WQ_minvals", "WQ_maxvals"];

    // Get the extent of the waterbody data
    values(
      catalog_url,
      variables,
      [
        [
          [0, 1, shapes[0]-1],
          [0, shapes[1]-1, shapes[1]-1],
          [0, shapes[2]-1, shapes[2]-1]
        ], [], [], [] // Getting all the medians and values
      ],
      (err, obj) => {
        if (err) { callback(err); }

        let data = obj["WQ_data"];
        data.shapes = shapes;
        data.time = data.time.map(t => moment.unix(t));
        data.startDate = data.time[0];
        data.variable = "WQ_data";
        data.medians = obj["WQ_medians"]["WQ_medians"];
        data.minvals = obj["WQ_minvals"]["WQ_minvals"];
        data.maxvals = obj["WQ_maxvals"]["WQ_maxvals"];
        // FIXME Duplicate values -_-
        if (!data.x) data.x = data.lon;
        if (!data.lon) data.lon = data.x;
        if (!data.y) data.y = data.lat;
        if (!data.lat) data.lat = data.y;
        if (!data.dates) data.dates = data.time;

        callback(null, data);
      });
  });

};

module.exports = {
  catalog,
  dimensions,
  constructUrl,
  variables,
  values,
  attributes,
  boundaries
};